<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 v-if="ProbationPeriodDetail" class="modal-title">{{ $t('ProbationPeriodDetail') }}</h4>
                    <h4 v-if="NoticePeriodDetail" class="modal-title">{{ $t('NoticePeriodDetail') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <tg-list :IsShowAction="false" ModuleName="Hiring" SubModuleCode="HR_JOB" :RenderRowActionMethod="renderActions" 
                        v-on:ActionButtonClick="actionButtonClick" IdentityColumn="policy_id" :HeaderText="$t('')" :listType="['List']"
                        :ListData="ItemData" :HeaderData="Headers" :TotalRecords="TotalRecord" :ListDataCallBackFunction="FetchData"  v-on:PagerButtonClick="pagerMethod" 
                        :showCheckBox="false" :SortExp="SortExp" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"
                        :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" :CurrentPageNumber="PageNumber" v-on:LeftsearchButtonClick="leftCommonSearch"> 
                        <template v-slot:slotdata="{ data }">
                            <template v-if="data.column.COLUMN_NAME == 'AssociatedEmployeeCount'">
                                <td class="text-center single-action"><a class="text-dark"><em class="fa fa-user"></em>
                                    <span class="noti-circle noti-blue">{{data.row.AssociatedEmployeeCount }}</span></a>
                                </td>                               
                            </template>
                            <template v-if="data.column.COLUMN_NAME == 'duration_type_id'">
                                <td class="text-center single-action">
                                    <span class="">{{data.row.duration}} {{data.row.duration_type_id }}</span>
                                </td>                               
                            </template>
                        </template>
                    </tg-list>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../../services/DataService';

export default {
    props: {
        ModalId: {
            type: Object,
            required: true
        },
        FromType: {
            type: Object,
            required: true
        },
        Name: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            NoticePeriodDetail: false,
            ProbationPeriodDetail: false,
            isLoading: false,
            ItemData: [],
            TotalRecord: 0,
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            Headers: [{
                    COLUMN_NAME: "department_name",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Department",
                    DISPLAY_ORDER: 1
                },
                {
                    COLUMN_NAME: "duration_type_id",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Duration",
                    DISPLAY_ORDER: 2
                },
                {
                    COLUMN_NAME: "days_type_id",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Days",
                    DISPLAY_ORDER: 3
                },
                {
                    COLUMN_NAME: "AssociatedEmployeeCount",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Employee Assigned",
                    DISPLAY_ORDER: 4
                }
            ],
            buttons: [{
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Close",
                onClick: this.Close,
            }],
        }
    },
    async created() {
        var vm = this;
        await vm.FetchData();
        if(vm.Name == 'ProbationPeriod'){
            vm.ProbationPeriodDetail = true;
        }else if (vm.Name == 'NoticePeriod'){
            vm.NoticePeriodDetail = true;
        }
    },
    methods: {
        leftCommonSearch: function (value) {},
        pagerMethod: function (value) {},
        sortdata: function (s) {},
        commonHeaderButtonClick: function (event) { },
        actionButtonClick: function (callbackfunction, item) {},
        renderActions: function (row) {},

        Close: function () {
            this.$parent.ShowManagePeriodDetail = false;
        },
        FetchData: async function () {
            var vm = this;
            var params = `Id=${vm.ModalId}&periodTpye=${vm.FromType}`
            await DataService.GetProbationNoticePeriod(params).then((response) => {
                if(response.data && response.data.length > 0){
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "AssociatedEmployeeCount") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }       
                        if (column.COLUMN_NAME == "duration_type_id") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }                  
                    });
                    vm.ItemData = response.data;
                }
            })
        }
    }
}
</script>
