<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" ModuleName="Policy" SubModuleCode="" :RenderRowActionMethod="renderActions" 
            IdentityColumn="id" :HeaderText="$t('Policies')" :listType="['List']"
            :ListData="PolicyData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord"
            :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" 
            v-on:LeftsearchButtonClick="leftCommonSearch" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" :CurrentPageNumber="PageNumber"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"> 
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'start_date'">
                    <td class="text-center single-action tourguide_a">
                        {{ $options.filters.formatDate(data.row.start_date) }}
                    </td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'status'">                   
                    <span v-bind:class="getStatusClassList(data.row.status, 'status')"
                        v-if="data.row.status == 1001">
                        {{ 'Active' }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.status, 'status')"
                        v-if="data.row.status == 1002">
                        {{ 'InActive' }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'ProbationPeriod'">
                    <a title="Probation Period View" href="javascript:;"  v-on:click="ProbationNotice(data.row.id, '0', 'ProbationPeriod')" class="action-link">
                        <em class="fa fa-file-text text-primary action-icon"></em>
                        <span class="text"></span>
                    </a>
                </template>
                    <template v-if="data.column.COLUMN_NAME == 'NoticePeriod'">
                    <a title="Notice Period View" href="javascript:;" v-on:click="ProbationNotice(data.row.id, '1', 'NoticePeriod')" class="action-link">
                        <em class="fa fa-wpforms text-info action-icon"></em>
                        <span class="text"></span>
                    </a>
                </template>
            </template> 
        </tg-list>
        <ManagePeriodDetail v-if="ShowManagePeriodDetail" :ModalId="ModalId" :FromType="FromType" :Name="Name"></ManagePeriodDetail>
        <AddPolicy v-if="ShowAddPolicy"></AddPolicy>
    </section>
</template>
    
<script>
import Schema from '../Policy/Schema';
import DataService from '../../../services/DataService';
import ManagePeriodDetail from '../Policy/ManagePeriodDetail';
import AddPolicy from './AddPolicy.vue';

export default {
    components: {
        ManagePeriodDetail, AddPolicy
    },
    data() {
        return {
            isLoading: false,
            ShowAddPolicy: false,
            ShowManagePeriodDetail: false,
            PolicyData: [],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            TotalRecord: 0,
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            PageSize: 10,
            TotalPages: 0,
            CurrentPage: 1,
            fromDate: '',
            toDate: '',
            ModalId: 0,
            FromType: '',
            Name: '',
            PolicyId: '',
            Headers: [{
                    COLUMN_NAME: "name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Policy Name",
                    DISPLAY_ORDER: 1
                },
                {
                    COLUMN_NAME: "description",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Description",
                    DISPLAY_ORDER: 2
                },
                {
                    COLUMN_NAME: "start_date",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Start Date",
                    DISPLAY_ORDER: 3
                },
                {
                    COLUMN_NAME: "ProbationPeriod",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Probation Period",
                    DISPLAY_ORDER: 4
                },
                {
                    COLUMN_NAME: "NoticePeriod",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Notice Period",
                    DISPLAY_ORDER: 5
                },
                {
                    COLUMN_NAME: "status",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Status",
                    DISPLAY_ORDER: 6
                },
            ]
        }
    },
    async created() {
        var vm = this;
        await vm.FetchData();
    },
    methods: {
        AddPolicy: function (PolicyId) {
            this.PolicyId = PolicyId;
            this.ShowAddPolicy = true;
            this.$router.push({ name: 'AddPolicy', params: { reqfrom: 'Policy/AddPolicy', id: PolicyId} });
        },
        ProbationNotice:function (Id, Type, Name) {
            var vm = this;
            vm.ModalId = Id;
            vm.FromType = Type;
            vm.Name = Name
            vm.ShowManagePeriodDetail = true;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 1001:
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 1002:
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        renderActions: function () { },
        leftCommonSearch: function (value) {
            var vm=this;
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if(value.searchConditionJson!=''){
            if (typeof value.searchCondition != 'undefined') {
                let str = value.searchConditionJson;
                let result = str.split('=')[1];
                this.searchCondition = result;
            }
            else {
                this.searchCondition = "";
            }
            }else{
                if(value.searchCondition!=''){
                const sqlString = value.searchCondition;
                const regex = /(\d{4}-\d{2}-\d{2})/g;
                const dates = sqlString.match(regex);
                vm.fromDate = dates[0];
                vm.toDate = dates[1]; 
            }else{
                this.searchCondition = "";
                vm.fromDate = '';
                vm.toDate = ''; 
            }
            }
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "AddPolicy":
                    this.AddPolicy(0, 'AddPolicy');
                    break;
            }
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            } else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {
                    var url = `ids=${val}&tablename=TALYGEN_HR_POLICY&key=id`;
                    DataService.DeleteItems(url).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Policy')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Policy')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            } else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        clickEventOfColumn: function (value, column) {
            var vm = this;
            if (column.COLUMN_NAME == 'name') {
                vm.$router.push({ name: 'AddPolicy', params: { id: value.id } });
            }
        },
        FetchData: async function () {
            var vm = this;
            var url = `fromDate=${vm.fromDate}&toDate=${vm.toDate}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&statusIds=`
            if (!vm.isBlank(this.searchCondition)) {
                url += `&search=${this.searchCondition}`;
            }
            await DataService.PolicyListing(url).then((response) => {
                if (response.data && response.data.length > 0) {
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "start_date") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "status") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "ProbationPeriod") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "NoticePeriod") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }       
                        if (column.COLUMN_NAME == 'name') {
                            column.settings = {
                                clickEvent: vm.clickEventOfColumn,
                                formatter: vm.columnDataFormatter
                            }
                        }                 
                    });
                    vm.PolicyData = response.data;
                    vm.TotalRecord = response.data[0].TOTAL_RECORD;
                    vm.TotalPages = Math.ceil(vm.TotalRecord / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecords = false;
                } else {
                    vm.PolicyData = [];
                    vm.TotalRecords = 0
                    vm.noRecords = true;
                }
                document.querySelector('input[placeholder="DateFrom"]').setAttribute('placeholder', 'Date From');
                document.querySelector('input[placeholder="DateTo"]').setAttribute('placeholder', 'Date To');
            })
        }
    }
}
</script>
